<template>
	<div>
		<div class="bigPosition">
			<p>{{title}}</p>
			<p class="posiP">{{content}}</p>
			<div>
				<span class="cancel" @touchstart="handleCancel">{{cancel}}</span>
				<span class="changePosi" @touchstart="handleOk">{{ok}}</span>
			</div>
		</div>
	</div>
</template>
<script>
	export default{
		name:"bigPosition"
	}
</script>
<style scoped>
	*{
		margin: 0;
		padding: 0;
	}
	.bigPosition{
		position: absolute;
		width: 200px;
		background: #eee;
		z-index: 1;
		left: 50%;
		top: 50%;
		transform: translate(-50%,-50%);
		font-size: 18px;
		text-align: center;
	}
	.bigPosition>p{
		padding-top: 10px;
		padding-bottom: 10px;
	}
	.posiP{
		border-bottom: 1px solid #ddd;
	}
	.cancel{
		display: inline-block;
		width: 95px;
		line-height: 35px;
		border-right: 1px solid #ddd;
		text-align: center;
	}
	.changePosi{
		display: inline-block;
		width: 95px;
		line-height: 35px;
		text-align: center;
	}
</style>


