import Vue from 'vue';
import positioning from './positioning';

/*用export跟export default都是导出文件的，但前者一个文件能用多个，
后者只能用一个，前者导出在用import接收是可以不用{}，但后者要用*/
export var message = (function(){
	var defaults = {//默认值
		title:"",
		content:"",
		cancel:"",
		ok:"",
		handleCancel:null,
		handleOk:null
	}
	/*vue中的extend方法，extend创建的是一个组件构造器，而不是一个具体的组件实例*/
	/*将positioning这个组件用extend创建，而不是让他在全局中注册*/
	/*下面写其script里的内容，并用document.body.appendChild( vm.$el );挂载*/
	var messageBox = Vue.extend(positioning);
	/*rel是sousou这个页面传过来的值*/
	return function(rel){
		for(var item in rel){
			defaults[item] = rel[item];
		}
		var vm = new messageBox({
			el:document.createElement('div'),
			data:{
				title:defaults.title,
				content:defaults.content,
				cancel:defaults.cancel,
				ok:defaults.ok
			},
			methods:{
				handleCancel(){
					/*改变这this指向*/
					defaults.handleCancel && defaults.handleCancel.call(this);
					/*移除这页面*/
					document.body.removeChild( vm.$el);
				},
				handleOk(){
					defaults.handleOk && defaults.handleOk.call(this);
					document.body.removeChild( vm.$el);
				}
			}
		});
		/*vm.$el获取Vue实例关联的DOM元素*/
		document.body.appendChild( vm.$el );
	}
})();


